import React from 'react'
import style from './Makro.module.css'


export default function Makros(props) {
    const {makro, yieldNr} = props
    
    // total daily nutrients / nr of servings
    const yieldQuantity = makro.quantity / yieldNr
    const yieldQuantityG = makro.quantityG / yieldNr

    // regulating color hue
    const colorHueConst = 2

    return (
        <div key={makro.label} className={style.scale}>
        <p>
            {makro.label}:{" "}
            {Math.round(yieldQuantity*10)/10}% / {Math.round(yieldQuantityG*10)/10}{makro.unitG}
        </p>
        <div className={style.bar}>
            <div 
            className={style.percentage} 
            style={yieldQuantity<150?{width:`${yieldQuantity}%`, filter:`hue-rotate(${yieldQuantity*colorHueConst}deg)`}:
            {width:`${yieldQuantity}%`, background: "#ff6bb5"}
            }>
                
            </div>
        </div>
    </div>
    )
}
