import React from 'react'
import style from './Pagination.module.css'

export default function Pagination(props) {
    const {nrOfPages, paginate, data} = props

    const pages = []
    for(let i = 1; i<=nrOfPages; i++){
        pages.push(i)
    }
    return (
        <nav className={style.nav}>
            {pages.map(page =>{
                return(
                    <div className={style.pageBox} key={page} onClick={()=>paginate(page)}>
                        <h1 className={style.pageTitle}>
                            {data[page-1].recipe.label}
                        </h1>
                        <img
                        className={style.pageImg} 
                        src={data[page-1].recipe.image} 
                        alt={data[page-1].recipe.label}/>
                    </div>
                )
            })}
        </nav>
    )
}
