
import React, {useState, useRef, useLayoutEffect} from 'react'
import style from './Recipe.module.css'
import Makro from './Makro'
import ingredientImg from './img/ingredient2.jpg'


export default function Recipe(props) {
    const {title, img, makrosInP, yieldNr, ingredients, healthLabel, makrosInG, searchFromTag, makroPortionsSwitch, makroPortions, makroSwitch, makroSwitchFun} = props

    const makroArray = []
    const foodCategoryTags = []

    
    //Take height of 6 elements and all elelemtns of makroArray, to make smooth expand / collapse column
    const [makroArrMinMax, setMakroArrMinMax] = useState(0)
    const makrosDiv = useRef()
    const copyMakroArray = useRef()


    //get height after element loads
    useLayoutEffect(()=>{
        setMakroArrMinMax({
            min:`${copyMakroArray.current.clientHeight}px`,
            max:`${makrosDiv.current.clientHeight}px`
        })
    }, [])


    // add units and quantities from makrosInG(gram) to makrosInP(%)
    for(let nutrient in makrosInP){
        makrosInP[nutrient]["quantityG"] = makrosInG[nutrient]['quantity']
        makrosInP[nutrient]["unitG"] = makrosInG[nutrient]['unit']
    }
    
    // add them to makroArray to map over them
    for(let m in makrosInP){
        makroArray.push(makrosInP[m])
    }

    // get 6 first elements of makroArray
    const copyMakroArray6 = makroArray.slice(0, 6)


    // add unique values to array
    for(let f in ingredients){
        if(!foodCategoryTags.includes(ingredients[f].foodCategory)){
            foodCategoryTags.push(ingredients[f].foodCategory)
        }
        else{
            continue
        }
    }
    return (
        <div className={style.main}>
            {/* Makros Section*/}
            <div className={style.makros}>
                <h2>Daily nutrients for </h2> 
                <h2 
                className={makroPortions?style.porActiv:style.porNotActiv}
                onClick={()=>makroPortionsSwitch(true)}>1 portion</h2>
                <hr/> 
                <h2 
                className={!makroPortions?style.porActiv:style.porNotActiv}
                onClick={()=>makroPortionsSwitch(false)}>{yieldNr} portions</h2>
                {/* Makros table */}
                <div 
                ref={makrosDiv} 
                className={style.makrosDiv} 
                style={!makroArrMinMax?null:!makroSwitch?{height:makroArrMinMax.max}:{height:makroArrMinMax.min}}>
                    {makroArray.map((singleMakro, index)=>{
                        return(
                            <Makro
                            key={index}
                            yieldNr={makroPortions?yieldNr:1} 
                            makro={singleMakro}
                            />

                        )
                    })}
                </div>
                {/* Expand/Collapse Button */}
                <div
                onClick={()=>makroSwitchFun(makroSwitch)}
                className={makroSwitch?`${style.makroSwitchButton} ${style.mSBExpand}`:`${style.makroSwitchButton} ${style.mSBCollapse}`}>
                    {makroSwitch?"Expand":"Collapse"}
                </div>
                {/* Copy array */}
                <div ref={copyMakroArray} style={{visibility:"hidden", position:"absolute"}} className="copyMakroArray">
                {copyMakroArray6.map((sM, index)=>{
                        return(
                            <Makro
                            key={index}
                            yieldNr={makroPortions?yieldNr:1} 
                            makro={sM}
                            />
                        )
                    })}
                </div>
            </div>
            
            {/* Info Section */}
            <div className={style.info}>
                <h1>{title}</h1>
                <img src={img} alt={title}/>
                <h2>Ingredients:</h2>
                <ol className={style["ing-ol"]}>
                {ingredients.map((i, index)=>{
                    return(
                        <div key={index} className={style['div-li']}>
                        <img 
                        className={style["img-li"]} src={i.image?i.image:ingredientImg} 
                        alt="ingredient"
                        />
                        <li 
                            className={style["ing-li"]} 
                        >
                            {i.text}
                        </li>
                        </div>
                    )
                })}
                </ol>
            </div>
            
            {/* Tags Section */}
            <div className={style.searchTags}>
                <h2>Food Categories:</h2>
                <div className={style.foodCat}>
                {foodCategoryTags.map((cat, index)=>{
                    return(
                        <p 
                        key={index}
                        onClick={()=>searchFromTag(cat)}
                        >{cat}</p>
                        
                    )
                })}
                </div>
                <h2>Health Labels:</h2>
                <div className={style.healthLabel}>
                {healthLabel.map((label, index)=>{
                    return <p 
                    key={index}
                    onClick={()=>searchFromTag(label)}
                    >{label}</p>
                })}
                </div>
            </div>

        </div>
    )
}