import React, {useEffect, useState, useRef} from 'react'
import Recipe from './Recipe'
import Pagination from './Pagination'
import style from './App.module.css'

function App() {
  const APP_ID = '90bae0c2'
  const APP_KEY = '484f5d15e48281f229b2030743146d09'

  const [recipes, setRecipes] = useState([])
  const [search, setSearch] = useState('')
  const [query, setQuery] = useState('chicken')
  const [currentPage, setCurrentPage] = useState(0)
  const [makroPortions, setMakroPortions] = useState(true)

  const inputField = useRef()

  const [makroSwitch, setMakroSwitch] = useState(true)
  const makroSwitchFun = bool =>{
      setMakroSwitch(!bool)
  }

  useEffect(()=>{
    fetch (`https://api.edamam.com/search?q=${query}&app_id=${APP_ID}&app_key=${APP_KEY}`)
    .then(response => response.json())
    .then(data => {
      console.log(data.hits);
      // console.log(data.hits[2].recipe.totalNutrients);
      setRecipes(data.hits)
    })

  }, [query])


  const updateSearch = e =>{
    setSearch(e.target.value)
  }
  const getSearch = e =>{
    e.preventDefault()
    setQuery(search)
  }

  const paginateFun = page =>{
    setCurrentPage(page-1)
    window.scrollTo(0, 0)
  }

  const searchFromTag = tagName =>{
    setSearch(tagName)
    inputField.current.focus()
  }

  // Switch makros from 1 portion to yieldNr portions
  //true - 1 portion, false yieldNr portions
  const makroPortionsSwitch = (bool) =>{
    setMakroPortions(bool)
}

  return (
    <div className={style.App}>
      <form onSubmit={getSearch} className={style["search-form"]}>
        <input
        placeholder="Search query"
        ref={inputField} 
        type="text" 
        className={style["search-bar"]} 
        value={search}
        onChange={updateSearch}
        />
        <button type="submit" className={style["search-button"]}>
          Search
        </button>
      </form>
      {/* If no recipes found, return message */}
      {recipes[currentPage]?
      <Recipe
        key={recipes[currentPage].recipe.calories}
        title={recipes[currentPage].recipe.label}
        yieldNr={recipes[currentPage].recipe.yield}
        makrosInP={recipes[currentPage].recipe.totalDaily}
        makrosInG={recipes[currentPage].recipe.totalNutrients}
        healthLabel={recipes[currentPage].recipe.healthLabels}
        img={recipes[currentPage].recipe.image}
        ingredients={recipes[currentPage].recipe.ingredients}
        searchFromTag={searchFromTag}
        makroPortions={makroPortions}
        makroPortionsSwitch={makroPortionsSwitch}
        makroSwitch={makroSwitch}
        makroSwitchFun={makroSwitchFun}
      />:
      `No recipes including ${query}`
      }

      <Pagination 
      nrOfPages={recipes.length} 
      paginate={paginateFun}
      data={recipes}
      />
    </div>
  );
}

export default App;
